.TodoTemplate {
  width: 512px;
  margin: 0 auto;
  margin-top: 6rem;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.164);

  .app-title {
    position: relative;
    color: white;
    background-color: #25c054;
    height: 4rem; //64
    font-weight: 600;
    letter-spacing: 0.4rem;
    font-size: 1.5rem; //24
    display: flex;
    justify-content: center;
    align-items: center;

    .leef1 {
      position: absolute;
      top: 0;
      left: 50px;
      width: 50px;
    }
    .leef2 {
      position: absolute;
      right: 50px;
      bottom: 0;
      width: 50px;
    }
  }
}

.content {
  background: white;
}